import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { SearchfilteraddComponent } from '../shared/searchfilter/searchfilteradd.component';
import { ManageviewnewComponent } from '../shared/manageviewnew/manageviewnew.component';
import { DatatableComponent, SelectionType } from '@swimlane/ngx-datatable';
import { ModuleList, CommonService } from '../shared/common.service';
import { WorkorderService } from './workorderservice.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { ConfirmationDialogService } from '../shared/confirmation-dialog/confirmation-dialog.service';
import { ModalDirective } from 'ngx-bootstrap';

@Component({
  selector: 'app-workorder',
  templateUrl: './workorder.component.html',
  styleUrls: ['./workorder.component.scss']
})
export class WorkorderComponent implements OnInit {

  @ViewChild('templateFilterView', { static: false }) FilterViewModal: SearchfilteraddComponent;
  @ViewChild('templateManageView', { static: false }) ManageViewModal: ManageviewnewComponent;
  @ViewChild(DatatableComponent, { static: false }) table: DatatableComponent;
  IsAuditCheckListDisplay: boolean = false;
  @ViewChild('audit', { static: false }) auditModel: ModalDirective;
  //@Input('moduleName') moduleName: any;
  //@Input('submoduleName') submoduleName: any;
  @Input() offset: number;
  moduleName = 'crm';
  submoduleName = 'workorder'; 
  custom_view_id = '';
  searchUserType = '';
  searchFilter = '';
  ViewModel: any = '';
  listFiltertext = '';
  loginuserid: any;
  vewId: any='';
  //modulePermission: ModuleList;
  loadSave = false;
  sortDir = 'desc';
  sortColumn = 'id';   
  lstUserType: any;
  pagedData: any = {
    pager: {},
    data: []
  };
  // listFilter = '';
  searchTxt = '';
  listFilter = '';
  lstPageSize: any
  pageSizeValue: number;
  SelectionType = SelectionType;
  //jsonData: any;
  jsonData: any = {
    data: [],
    schema: []
  };
  columndata: any;
  columnheading: any;
  totalRecords: any;
  currentPage: number;
  companyId: any;
  tableName = 'tblWorkorderNew'; //tblProducts';
  deleteId: any;
  selected = [];
  is_filter: any;
  SearhName: any;
  lstListingColorCode: any;

  modulePermission: any[] = [];
  isAdd: boolean = false;
  isUpdate: boolean = false;
  isDelete: boolean = false;
  myCheckbox: any = false;
  isDisabled: any;
  auditData: any;


  constructor(private workorderService: WorkorderService, private dialogService: ConfirmationDialogService,
    private commonService: CommonService, private router: Router,
    private activeRoute: ActivatedRoute
    , private toaster: ToastrService) {
    
    this.commonService.getLoggedInName.subscribe((userdetail: any) => {
      this.loginuserid = userdetail.id;
      this.companyId = userdetail.companyId;
    });

    this.commonService.getMasterItemsList("usertype").subscribe((result: any) => {
      this.lstUserType = this.commonService.masters;
    })
    const moduleCode = this.activeRoute.snapshot.data.moduleCode;
    this.modulePermission = this.commonService.getPermissiondata(moduleCode);

    let add = this.modulePermission.find(m => m.privilegecode.toUpperCase() == 'WORKORDERADD');
    if (add == undefined) {
      add = "null";
    } else {
      this.isAdd = true;
    }

    let update = this.modulePermission.find(m => m.privilegecode.toUpperCase() == 'WORKORDERUPDATE');
    if (update == undefined) {
      update = "null";
    } else {
      this.isUpdate = true;
    }
   

    let deletedata = this.modulePermission.find(m => m.privilegecode.toUpperCase() == 'WORKORDERDELETE');
    if (deletedata == undefined) {
      deletedata = "null";
    } else {
      this.isDelete = true;
    }
   
  }

  ngOnInit() {
    this.loadSave = true;

    this.SearhName = '';
    this.custom_view_id = '';
    this.pageSizeValue = 15;
    this.currentPage = 1;
    this.getPageSizes();
    this.LoadViewData();
    this.refresh();
  }

  GetcustomViewid(event) {
    if (event == 'undefined' || typeof event == 'undefined') {
      this.LoadViewData();
    }
    this.pagedData.data.forEach(cnt => {
      if (cnt.custom_view_id == event) {
        this.ViewModel = cnt.view_name;
      }
    });
    this.vewId = event;
    this.custom_view_id = event;
    this.refresh();
  }

  refresh() {
    this.selected = [];
    this.deleteId = null;
    this.loadSave = true;
    this.workorderService.GetWorkOrderList(this.listFiltertext, this.sortColumn, this.currentPage, this.pageSizeValue, this.sortDir, this.loginuserid, this.moduleName, this.submoduleName, this.companyId, this.custom_view_id, this.myCheckbox)
      .subscribe(response => {
        this.jsonData = response;
        this.columndata = this.jsonData.data;
        this.columnheading = this.jsonData.schema;
        this.totalRecords = this.columndata.length > 0 ? this.columndata[0].total_records : 0;
        this.offset = this.currentPage;

      }, error => {
        this.loadSave = false;
      }, () => { this.loadSave = false; });

  }
  get curPage(): number {
    return this.offset;
  }
  getPageSizes() {
    this.commonService.getMasterItemsList("PageSize").subscribe((res: any) => {
      this.lstPageSize = this.commonService.masters;
    })
  }
  setPage($event) {
    this.loadSave = true;
    this.currentPage = $event.page;
    this.refresh();
  }

  onSort($event) {
    const sort = $event.sorts[0]
    this.sortDir = sort.dir;
    this.sortColumn = sort.prop;
    this.currentPage = 1;
    this.refresh();
  }
  displayDetailDetail(TemplateName: any) {
    this.ManageViewModal.show(TemplateName);
  }

  updateFilter(event) {
    //this.SearhName = event.target.value;
    this.listFilter = event.target.value;
    this.listFiltertext = "workorder.WorkOrderNumber like '%" + this.listFilter + "%'";
    let keycode = (event.keyCode ? event.keyCode : event.which);
    if (keycode === 13 || keycode === '13') {
      this.refresh();
    }
  }
  onChange($event) {
    this.currentPage = 1;
    this.refresh();
  }
  search() {
    this.listFiltertext = '';
    this.listFiltertext = "workorder.WorkOrderNumber like '%" + this.listFilter + "%'";
    this.refresh();
  }

  reset() {

    this.listFilter = '';
    this.listFiltertext = '';
    this.currentPage = 1;
    this.sortDir = 'desc';
    this.sortColumn = 'Id';
    this.myCheckbox = false;
    this.refresh();
  }

  onSelect({ selected }) {

    if (this.deleteId == "" || this.deleteId == null || this.deleteId == 'undefined') {
      this.selected.splice(0, this.selected.length);
      this.selected.push(...selected);
      this.deleteId = null;
      this.deleteId = "";
      for (let i = 0; i < selected.length; i++) {
        this.deleteId += selected[i].id.toString() + ",";
      }

    }
    else {
      this.deleteId = null;
      this.deleteId = "";
      for (let i = 0; i < selected.length; i++) {
        this.deleteId += selected[i].id.toString() + ",";
      }
    }
  }
  popUpOpen() {

    this.is_filter = '';
    this.is_filter = this.listFiltertext.length;
    this.FilterViewModal.show(this.companyId, this.is_filter);
  }
  deleteAll() {
    if (this.deleteId != null && this.deleteId != "") {
      const message = `Are you sure you want to delete Work Order(s)"`;
      this.dialogService.confirm('Delete Work Order(s)', message).subscribe(confirmed => {
        if (confirmed) {
          this.workorderService.DeleteRecords(this.deleteId, this.tableName).subscribe(r => {
            this.toaster.success(`Record(s) has been deleted successfully`);
            this.deleteId = "";
            this.selected = [];
            this.refresh();
          }, error => {
          });
        }
      });
    }
    else {
      this.toaster.error("Please select at least one row.");
    }
  }

  Delete(row: any) {
    const message = `Are you sure you want to delete Work Order  "${row.Subject}"?`;
    this.dialogService.confirm('Delete Work Order', message).subscribe(confirmed => {
      if (confirmed) {
        this.workorderService.DeleteRecords(row.id, this.tableName).subscribe(r => {
          this.toaster.success(`Work Order "${row.Subject}" has been deleted successfully`);
          this.refresh();
        }, error => {
        });
      }
    });
  }
  ApplyAdvanceFilter(event) {
    this.currentPage = 1;
    this.listFiltertext = '';
    this.listFiltertext = event;
    this.refresh();
  }
  getListingColorCode(fieldValue: string) {
    this.lstListingColorCode = '';
    if (fieldValue != null) {
      this.lstListingColorCode = fieldValue.split('::');
      if (this.lstListingColorCode.length > 0) {
        this.lstListingColorCode = [{ color: this.lstListingColorCode[0], colorkey: this.lstListingColorCode[1] }]
      }
    }
    return this.lstListingColorCode;

  }

  LoadViewData() {
    this.loadSave = true;
    this.pageSizeValue = 15;
    this.currentPage = 1;
    this.commonService.getViewList(this.searchTxt, this.searchUserType, this.sortColumn, this.sortDir, 0, this.pageSizeValue, this.moduleName, this.submoduleName, this.companyId).subscribe(response => {
      this.pagedData = this.commonService.pagedData;
      this.pagedData.data.forEach(cnt => {
        if (cnt.is_default == true) {
          this.vewId = cnt.custom_view_id;
          this.ViewModel = cnt.view_name;
          this.custom_view_id = cnt.custom_view_id;
        }
      });

      this.loadSave = false;
    }, error => {
      this.loadSave = false;
    })
  }

  SetManageViewValue(event, text) {
    this.ViewModel = text;
    this.custom_view_id = event;
    this.refresh();
    //this.LoadViewData();
  }
  switchClicked(event) {
    this.listFiltertext = '';
    this.myCheckbox = event.srcElement.checked;
    this.currentPage = 1;
    if (this.listFilter.trim().length > 0) {
      this.listFiltertext = "workorder.WorkOrderNumber like '%" + this.listFilter + "%'";
    }
    if (this.listFiltertext.trim().length > 0) {
      this.refresh();
    }
    if (this.myCheckbox == false) {
      this.refresh();
    }
  }


}
