import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WorkorderComponent } from './workorder.component';
import { ManageworkorderComponent } from './manageworkorder.component';
import { ViewworkorderComponent } from './viewworkorder.component';

const routes: Routes = [
  { path: '', component: WorkorderComponent },
  { path: 'add', component: ManageworkorderComponent },
  { path: 'edit/:id', component: ManageworkorderComponent },
  { path: 'view/:id', component: ViewworkorderComponent }
  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorkOrderRoutingModule { }
